<script >
import Cookies from 'js-cookie';

export default {
  setup() {
    //0=英文,1=中文
   const change = (x)=>{
    if(x ==0){
        Cookies.set('lang' , 0, { expires: 30 });
    }
    else{
        Cookies.set('lang' , 1, { expires: 30 });
    }
    window.location.reload();
   }

   return {change};
  }
}

</script>
  
