import Home from '../components/IndexHi.vue';
import blog from '../components/WinBlog.vue';
import App from '../components/AppAn.vue';
import Game from '../components/GameMo.vue';
import Other from '../components/OtherWe.vue';
import Pay from '../components/PayS.vue';
import { createWebHashHistory, createRouter } from "vue-router";

const routes =[
    {
      path: "/",
      name: "Home",
      component: Home,
    }, {
      path: "/blog",
      name: "blog",
      component: blog,
    }, {
      path: "/App",
      name: "App",
      component: App,
    },{
      path: "/Game",
      name: "Game",
      component: Game,
    },{
      path: "/Other",
      name: "Other",
      component: Other,
    },{
      path: "/Pay",
      name: "Pay",
      component: Pay,
    },
  ];

const router = createRouter(
    { history: createWebHashHistory(),
      base: '/ArlanW/',
       routes })

export default router;