<template>
  <NavAndAll/>
  <div style="width: 100%; height: 100vh;" ref="changepage">
  <div id="hr"></div>
    <router-view/>
</div>
</template>

<script>
import NavAndAll from './components/NavAndAll.vue'
import Hammer from 'hammerjs';

export default {
  name: 'App',
  components: {
   NavAndAll,
  },
  mounted() {
    const changepage = this.$refs.changepage;
    const hammer = new Hammer(changepage);

    hammer.on('swipeleft', () => {
      switch (this.$route.name) {
      case 'Home':
        this.$router.push({ name: 'blog' });
        break;
      case 'blog':
        this.$router.push({ name: 'App' });
        break;
      case 'App':
        this.$router.push({ name: 'Game' });
        break;
      case 'Game':
        this.$router.push({ name: 'Other' });
        break;
      case 'Other':
        this.$router.push({ name: 'Pay' });
        break;
      default:
        // 如果没有匹配到任何路由，可以选择跳转到默认页面或执行其他逻辑
        this.$router.push({ name: 'Home' });
    }
    });
    hammer.on('swiperight', () => {
  switch (this.$route.name) {
    case 'Pay':
      this.$router.push({ name: 'Other' });
      break;
    case 'Other':
      this.$router.push({ name: 'Game' });
      break;
    case 'Game':
      this.$router.push({ name: 'App' });
      break;
    case 'App':
      this.$router.push({ name: 'blog' });
      break;
    case 'blog':
      this.$router.push({ name: 'Home' });
      break;
    default:
      // 如果没有匹配到任何路由，可以选择跳转到默认页面或执行其他逻辑
      this.$router.push({ name: 'Home' });
  }
});

  }
}

</script>

<style>
  body{
    background-color: #111 !important;
    overflow: hidden;
}
  #hr{
    position: fixed;
    width: 110vw;
    top:0%;
    left: -5vw;
    height: 1px;
    background-color: rgb(0, 104, 71);
    animation-name:hrMove,hrColor;
    animation-duration:4s, 1.5s;
    animation-timing-function: linear,linear;
    animation-iteration-count: infinite,infinite; 
    z-index: -1;
  }

  @keyframes hrMove {
  from {top:0%;}
  to {top:100%;}}

  @keyframes hrColor {
  from {background-color: rgb(0, 104, 71);}
  to {background-color: #111;}}
</style>
